<template>
  <div class="pos">
    <div>
      <el-row>
        <el-col :span="7" id="order-list" class="pos-order">
          <el-tabs>
            <el-tab-pane label="点餐">
              <el-table :data="tableData" border style="width：100%">
                <el-table-column prop="goodsName" label="商品"></el-table-column>
                <el-table-column prop="count" label="数量"></el-table-column>
                <el-table-column prop="price" label="单价"></el-table-column>
                <el-table-column label="操作" width="100" fixed="right">
                  <template slot-scope="scope">
                    <el-button type="text" size="small" @click='delSingleData(scope.row)'>删除</el-button>
                    <el-button type="text" size="small" @click='addOrderList(scope.row)'>增加</el-button>
                  </template>
                </el-table-column>
              </el-table>
              <div class="total">
                <span>
                  <small>数量：</small> {{totalCount}}
                </span>
                <span>
                  <small>金额：</small>{{totalMoney}}元
                </span> 
              </div>
              <div>
                <el-button type="warning">挂单</el-button>
                <el-button type="danger" @click="delAllGoods()">删除</el-button>
                <el-button type="success" @click="checkOut()">结账</el-button>
              </div>
              
            </el-tab-pane>
            <el-tab-pane label="挂单">挂单</el-tab-pane>
            <el-tab-pane label="外卖">外卖</el-tab-pane>
          </el-tabs>
        </el-col>
        <el-col :span="17">
          <div class="often-goods">
            <div class="title">常用商品</div>
            <div class="often-goods-list">
              <ul>
                <li v-for="goods in oftenGoods" @click='addOrderList(goods)'>
                  <span>{{goods.goodsName}}</span>
                  <span class="o-price">¥{{goods.price}}元</span>
                </li>
              </ul>
            </div>
          </div>
          <div class="goods-type">
            <el-tabs>
              <el-tab-pane label="汉堡">
                <ul class="cookList">
                  <li v-for="goods in type0Goods" @click='addOrderList(goods)'>
                    <span class="foodImg"><img :src="goods.goodsImg" alt=""></span>
                    <span class="foodName">{{goods.goodsName}}</span>
                    <span class="foodPrice">¥{{goods.price}}元</span>
                  </li>
                </ul>
              </el-tab-pane>
              <el-tab-pane label="小食">
                <ul class="cookList" >
                  <li v-for="goods in type1Goods" @click='addOrderList(goods)'>
                    <span class="foodImg"><img :src="goods.goodsImg" alt=""></span>
                    <span class="foodName">{{goods.goodsName}}</span>
                    <span class="foodPrice">¥{{goods.price}}元</span>
                  </li>
                </ul>
              </el-tab-pane>
              <el-tab-pane label="饮料">
                <ul class="cookList">
                  <li v-for="goods in type2Goods" @click='addOrderList(goods)'>
                    <span class="foodImg"><img :src="goods.goodsImg" alt=""></span>
                    <span class="foodName">{{goods.goodsName}}</span>
                    <span class="foodPrice">¥{{goods.price}}元</span>
                  </li>
                </ul>
              </el-tab-pane>
              <el-tab-pane label="套餐">
               <ul class="cookList">
                  <li v-for="goods in type3Goods" @click='addOrderList(goods)'>
                    <span class="foodImg"><img :src="goods.goodsImg" alt=""></span>
                    <span class="foodName">{{goods.goodsName}}</span>
                    <span class="foodPrice">¥{{goods.price}}元</span>
                  </li>
                </ul>
              </el-tab-pane>
            </el-tabs>
          </div>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
var mark = 0;
var now;

export default {
  name:'Pos',
  data(){
    return{
      tableData: [],
      oftenGoods:[],
      type0Goods:[],
      type1Goods:[],
      type2Goods:[],
      type3Goods:[],
      totalCount:0,
      totalMoney:0,
    }
  },
  mounted:function(){
    document.getElementById('order-list').style.height = document.body.clientHeight + 'px';
  },
  created(){
    axios.get('http://jspang.com/DemoApi/oftenGoods.php')
    .then(response=>{
      // console.log(response);
      this.oftenGoods = response.data;
    })
    .catch(err=>{
      console.log(err);
      alert('网络错误不能访问');
    }),
    axios.get('http://jspang.com/DemoApi/typeGoods.php')
    .then(response=>{
      // console.log(response);
      this.type0Goods = response.data[0];
      this.type1Goods = response.data[1];
      this.type2Goods = response.data[2];
      this.type3Goods = response.data[3];
    })
    .catch(err=>{
      console.log(err);
      alert('网络错误，不能访问');
    });
  },
  methods:{
    //添加商品
    addOrderList(goods){
      let isHave = false;
      //判断订单列表里是否有选中的商品
      for(let i = 0; i<this.tableData.length; i++){
        if(this.tableData[i].goodsId == goods.goodsId){
          isHave = true;
        }
      };
      //如果商品已存在，加1 否则添加一个商品
      if(isHave){
        let arr = this.tableData.filter(o=>o.goodsId == goods.goodsId);
        arr[0].count++;
      }else{
        let newGoods={goodsId:goods.goodsId,goodsName:goods.goodsName,price:goods.price,count:1};
        this.tableData.push(newGoods);
      };
      this.getAllMoney();
    },
    //删除单个商品
    delSingleData(goods){
      this.tableData = this.tableData.filter(o=>o.goodsId != goods.goodsId);
      this.getAllMoney();
    },
    //进行数量金额的汇总计算
    getAllMoney(){
      this.totalCount = 0;
      this.totalMoney = 0;
      if(this.tableData){
        this.tableData.forEach((element)=>{
          this.totalCount += element.count;
          this.totalMoney = this.totalMoney +( element.count * element.price);
        });
      }
    },
    //删除全部商品
    delAllGoods(){
      now = Date.now();
      if(now - mark < 3000){
        return;
      }else{
        if(this.tableData != ''){
          this.tableData = [];
          this.totalCount = 0;
          this.totalMoney = 0;
        }else{
          this.$message.error('商品列表已空！！！');
        }
        mark = now;
      }
      
      
    },
    //模拟结账
    checkOut(){
      now = Date.now();
      if(now - mark < 3000){
        return;
      }else{
        if(this.totalCount != 0){
          this.tableData = [];
          this.totalCount = 0;
          this.totalMoney = 0; 
          this.$message({
            message:'结账成功，感谢你又为店里做出了一份努力！！！！',
            type:'success'
          });
        }else{
          this.$message.error('不能空结，老板了解你急切的心情');
        }
        mark = now;
      }
      
      
    }
  }

}
</script>

<style scoped>
  .pos-order{
    background-color: #f9fafc;
    border-right: 1px solid #c0ccda;
  }
  .title{
    height: 20px;
    border-bottom: 1px solid #d3dce6;
    background-color: #f9fafc;
    padding: 10px;
  }
  .often-goods-list ul li{
    list-style: none;
    float: left;
    border: 1px solid #e5e9f2;
    border-radius: 5px;
    padding: 10px; margin: 5px;
    background-color: #ffffff;
    cursor: pointer;
  }
  .often-goods-list ul li:hover{
    box-shadow: 0px 0px 1px 1px #58b7ff;
  }
  .o-price{
    color: #58b7ff;
  }
  .goods-type{
    clear: both;
  }
  .cookList li{
    list-style: none;
    width:23%;
    border:1px solid #E5E9F2;
    border-radius: 5px;
    height: auto;
    overflow: hidden;
    background-color:#fff;
    padding: 2px;
    float:left;
    margin: 2px;
    cursor: pointer;
   }
   .cookList li:hover{
     box-shadow: 0px 0px 1px 1px #58b7ff;
   }
   .cookList li span{
        display: block;
        float:left;
   }
   .foodImg{
       width: 40%;
   }
   .foodImg img{
     width:100%;
   }
   .foodName{
       font-size: 18px;
       padding-left: 10px;
       color:brown;
   }
   .foodPrice{
       font-size: 16px;
       padding-left: 10px;
       padding-top:10px;
   }
   .total{
     height: 50px;
   }
   .total>span{
     display: inline-block;
     width: 100px; line-height:50px; 
   }


</style>




